<template>
  <div>
    <div v-if="isActive =='0'">
      <choose></choose>
    </div>
    <div v-if="isActive =='1'">
      <conversation></conversation>
    </div>
    <div v-if="isActive =='2'">
      <message></message>
    </div>
    <div>
      <p>
        {{JSON.stringify(userId)}}
        {{isActive}}
      </p>
      {{loading}}
      <p v-if="loading">等待中.....</p>
    </div>
  </div>
</template>

<script>
import choose from "../components/choose";
import conversation from "../components/conversation";
import message from "../components/message";
import userList from '../commen/tim/user'

export default {
  name: "index",
  data() {
    return {
      isActive: 0,
      userId: "",
      loading: true,
      userInfo:'',

    };
  },
  components: {
    choose,
    conversation,
    message
  },
  methods: {
    //登录tim
    loginTim() {
        userList.forEach(item=>{
            if(item.userId == this.userId){
                this.userInfo = item
            }
        })

        if(this.userInfo){
            this.tim.login({
          userID: this.userInfo.userId,
          userSig: this.userInfo.userSig
        })
        .then((res) => {
          this.loading = false;
          this.$store.commit("toggleIsLogin", true);
          this.$store.commit("startComputeCurrent");
          //   this.$store.commit('showMessage', {
          //     type: 'success',
          //     message: '登录成功'
          //   })
          console.log(res)
        })
        .catch(error => {
          this.loading = false;
          console.log(error)
          //   this.$store.commit('showMessage', {
          //     message: '登录失败：' + error.message,
          //     type: 'error'
          //   })
        });
        }



   
  
 
 },

  },
  created() {
    this.userId = "6";
   

    this.loginTim()
  },
  mounted() {

  }
};
</script>

<style lang="scss" scoped>
</style>